.footer {
    background-color: #333333;
    color: #ffffff;
    padding: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  .footer-section {
    margin-right: 60px;
    width: 60%;
  }

  
  .footer-heading {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .footer-text {
    margin: 0;
    margin-bottom: 5px;
    line-height: 1.5rem;
  }
  
  .footer-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .footer-list li {
    margin-bottom: 5px;
  }
  
  .footer-list a {
    text-decoration: none;
    color: #ffffff;
  }
  
  .footer-list a:hover {
    text-decoration: underline;
  }
  .footer-flex{
    display: flex;
    justify-content: space-between;
  }