.cart-container {
    padding: 20px;
    min-height: 52vh;
    margin-top: 60px;
  }
  
  .cart-title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .cart-items-container {
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  
  .cart-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .cart-total-text {
    font-weight: bold;
    font-size: 18px;
  }
  
  .cart-clear-button {
    background-color: #ff6347;
    color: white;
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .cart-empty {
    font-size: 18px;
    text-align: center;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  