/* Desktop styles */

.header-nav {
  background-color: #333333;
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
}

.header-list {
  list-style-type: none;
  margin: 0;
  margin-left: 50%;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.header-item {
  margin-right: 20px;
}

.header-link {
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
}

.header-link:hover {
  /* text-decoration: underline; */
}

.header-link:active {
  color: yellowgreen;
}

/* Tablet styles */

@media (max-width: 1024px) {
  .header-list {
    margin-left: 0;
    justify-content: center;
  }
  
  .header-item {
    margin-right: 10px;
    margin-left: 10px;
  }
}

/* Mobile styles */

@media (max-width: 767px) {
  .header-list {
    flex-direction: column;
    align-items: center;
  }
  
  .header-item {
    margin: 5px 0;
  }
}
.menu-toggle {
  display: none;
}

@media (max-width: 1024px) {
  .menu-toggle {
    display: block;
    background-color: inherit;
    font-size: 1.3rem;
    color: white;
    border: none;
  }

  .header-list {
    flex-direction: column;
    align-items: center;
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    background-color: #333333;
    padding: 10px;
  }

  .menu-open {
    display: flex;
    flex-direction: column;
  }

  .header-item {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .header-link {
    color: #ffffff;
    text-decoration: none;
    font-weight: bold;
  }

  .header-link:hover {
    text-decoration: underline;
  }

  .active {
    color: yellowgreen;
  }
}
