.product-list-container {
    margin: 0 auto;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    /* justify-content: center;  */
    align-items: center;
    margin-bottom: 20px;
  }
  
  .product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:15px;
  }
  
  @media screen and (max-width: 500px) {
    .product-list {
      align-items: center;
      flex-direction: column;
    }
  }
  