  .not-found-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
    color: #333;
  }

  .not-found-heading {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .not-found-text {
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 2rem;
  }